$mt-breakpoints: (
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1200px,
  xxl: 1400px,
);

:host {
  --mt-imagem-floated-image: null;
  --mt-imagem-floated-direction: null;
  --mt-imagem-floated-margin: null;
  display: block;

  &::after {
    content: '';
    display: table; /* or block */
    clear: both;
  }
}

::slotted(img) {
  shape-outside: var(--mt-imagem-floated-image);
  float: var(--mt-imagem-floated-direction);
  margin: var(--mt-imagem-floated-margin);
  max-width: 100%;
}
