import { Component, h, Host } from '@stencil/core';

@Component({
  tag: 'mt-floated',
  styleUrl: 'floated.scss',
  scoped: true,
})
export class Floated {
  render() {
    return (
      <Host class="mt-floated">
        <div>
          <slot name="image-right"></slot>
          <slot name="text"></slot>
          <slot name="image-left"></slot>
        </div>
      </Host>
    );
  }
}
